.signup-body {
  background: #fff;
  overflow-x: hidden;
  /* overflow-y: hidden; */
  font-family: "Arimo", sans-serif !important;
  color: #151515;
  margin: 0;
  padding: 0;
  width: 96%;
  padding-left: 2%;
  padding-top: 30px;
}

.signup-illus3 {
  /* position: fixed; */
  margin-top: -80px;
  margin-left: 30px;
}

.signup-illus {
  height: 400px;
  width: 400px;
  margin-top: 420px;
  margin-left: -150px;
  position: fixed;
}

.signup-illus4 {
  display: none;
}

.signup-mid {
  /* border: 1px solid red; */
  margin-top: 50px;
  z-index: 999;
}

.signup-mid h1 {
  text-align: center;
  margin-top: 80px;
  /* font-family: "Roboto"; */
}

.signup-mid p {
  font-family: "Roboto";
}
.form-container {
  justify-content: center;
  text-align: center;
}
.subtitle {
  /* padding-left: 10%; */
  text-align: center;
  font-size: 22px;
  font-family: Roboto;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
}
.signup-form {
  margin-top: 15px;
  justify-content: center;
  text-align: center;
}

.signup-input {
  margin-top: 10px !important;
  background-color: white;
  /* margin-bottom: 5px !important; */
}

.signup-form-control {
  display: block;
  width: 397px;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  cursor: text;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

label {
  padding: 5px 3px;
}

.tele {
  margin-top: 10px !important;
}

.signup-form-row {
  margin: auto !important;
  width: 150%;
  margin-left: 100px;
}

.signup-button {
  background: linear-gradient(to right, #0066cc 0%, #66ffff 100%);
  border: none !important;
  /* padding: 10px 0px !important; */
  color: #151515 !important;
  width: 180px !important;
  border-radius: 25px !important;
  margin: auto;
  height: 40px;
  margin-bottom: 20px;
}

.form-row p {
  padding-top: 20px !important;
  margin: auto !important;
}

.signup-button:focus,
.signup-button:active:focus,
.signup-button.active:focus,
.signup-button.focus,
.signup-button:active.focus,
.signup-button.active.focus {
  outline: none !important;
  outline: 0 !important;
}

.signup-illus2 {
  width: 1400px;
  /* height: 500px; */
  margin-top: -150px;
  margin-left: -670px;
  position: fixed;
}

@media screen and (max-width: 992px) {
  #welcome {
    font-size: 24px !important;
  }

  .signup-illus2 {
    display: none;
  }

  .signup-illus {
    display: none;
  }
  .subtitle {
    font-size: 16px;
  }
}

@media screen and (max-width: 568px) {
  .signup-mid {
    margin-top: -160px;
  }
  .signup-form-control {
    display: block;
    width: 322px;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  .signup-illus4 {
    display: block;
    height: auto;
    width: 106%;
    margin: 0;
    padding: 0;
    margin-left: -2%;
    position: fixed;
    bottom: 0;
  }
}

