  padding: 8px 12px;
  cursor: pointer;
}

ul.tag-list li label::before {
  display: inline-block;
  font-style: normal;
  font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-weight: 900;
  font-size: 14px !important;
  padding: 2px 6px 2px 2px;
  transition: transform 0.3s ease-in-out;
}

ul.tag-list li input[type="checkbox"]:checked + label {
  border: 2px solid rgba(172, 210, 235, 0.5);
  background-color: rgba(172, 210, 235, 0.5);
  color: black;
  transition: all 0.2s;
}

ul.tag-list li input[type="checkbox"] {
  display: absolute;
}

ul.tag-list li input[type="checkbox"] {
  position: absolute;
  opacity: 0;
}

ul.tag-list li input[type="checkbox"]:focus + label {
  border: 2px solid rgba(172, 210, 235, 0.5);
}

