.tagsinput .tag {
  position: relative;
  background: rgba(172, 210, 235, 0.5);
  display: block;
  max-width: 100%;
  word-wrap: break-word;
  color: black;
  padding: 5px 30px 5px 5px;
  border-radius: 2px;
  margin: 0 5px 5px 0;
}

.tagsinput .tag .tag-remove {
  position: absolute;
  background: 0 0;
  display: block;
  width: 30px;
  height: 30px;
  top: 0;
  right: 0;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  color: black;
  line-height: 30px;
  padding: 0;
  border: 0;
}

.tagsinput .tag .tag-remove:after,
.tagsinput .tag .tag-remove:before {
  background: black;
  position: absolute;
  display: block;
  width: 10px;
  height: 2px;
  top: 14px;
  left: 10px;
  content: "";
}

.tagsinput .tag .tag-remove:before {
  transform: rotateZ(45deg);
}

.tagsinput .tag .tag-remove:after {
  transform: rotateZ(-45deg);
}

.tagsinput div {
  flex-grow: 1;
}

