.tagsinput {
  display: flex;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.9);
  font-size: 14px;
  line-height: 20px;
  color: black;
  padding: 5px 5px 0;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  margin-left: 0;
}

.tagsinput.focus {
  border-color: #ccc;
}

.tagsinput .tag {
  position: relative;
  background: rgba(172, 210, 235, 0.5);
  display: block;
  max-width: 100%;
  word-wrap: break-word;
  color: black;
  padding: 5px 30px 5px 5px;
  border-radius: 2px;
  margin: 0 5px 5px 0;
}

.tagsinput .tag .tag-remove {
  position: absolute;
  background: 0 0;
  display: block;
  width: 30px;
  height: 30px;
  top: 0;
  right: 0;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  color: black;
  line-height: 30px;
  padding: 0;
  border: 0;
}

.tagsinput .tag .tag-remove:after,
.tagsinput .tag .tag-remove:before {
  background: black;
  position: absolute;
  display: block;
  width: 10px;
  height: 2px;
  top: 14px;
  left: 10px;
  content: "";
}

.tagsinput .tag .tag-remove:before {
  transform: rotateZ(45deg);
}

.tagsinput .tag .tag-remove:after {
  transform: rotateZ(-45deg);
}

.tagsinput div {
  flex-grow: 1;
}

.input_tag {
  background: 0 0;
  background-color: white;
  display: block;
  /* width: 100%; */
  font-size: 14px;
  line-height: 20px;
  padding: 5px;
  border: 0;
  margin: 0 5px 5px 0;
  color: black;
}

.tagsinput div input.error {
  color: black;
}

.tagsinput div input::-ms-clear {
  display: none;
}

.tagsinput div input::-webkit-input-placeholder {
  color: rgb(0, 0, 0);
  opacity: 1;
}

.tagsinput div input:-moz-placeholder {
  color: black;
  opacity: 1;
}

.tagsinput div input::-moz-placeholder {
  color: black;
  opacity: 1;
}

.tagsinput div input:-ms-input-placeholder {
  color: black;
  opacity: 1;
}

